.search-form {
  z-index: 100;
  background: linear-gradient(#82bcce 0%, #1c6ea4 0%, #82bcce 100%);
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  box-shadow: 0 3px 5px #00000080;
}

input[type="text"] {
  border: none;
  border-radius: 5px;
  outline: none;
  width: 300px;
  height: 50px;
  padding-left: 15px;
  font-size: 20px;
}

@media screen and (min-width: 650px) {
  input[type="text"] {
    width: 350px;
  }
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #2373a9;
  border: 2px solid #216b9dcb;
  border-radius: 5px;
  outline: none;
  width: 80px;
  height: 50px;
  margin-left: 10px;
  padding-top: 5px;
  padding-bottom: 10px;
  font-size: 16px;
  font-weight: 400;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
  box-shadow: 0 4px 4px #00000026;
}

button[type="submit"]:hover {
  transform: scale(1.05);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 10px;
  min-width: 320px;
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 75px;
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
}

@media screen and (min-width: 650px) {
  .gallery {
    width: 760px;
  }
}

@media screen and (min-width: 1310px) {
  .gallery {
    width: 1440px;
  }
}

.photo-card {
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;
  height: 360px;
  margin: 0;
  display: flex;
  overflow: hidden;
  box-shadow: 0 3px 8px #00000080;
}

.photo {
  object-fit: cover;
  width: 100%;
  height: 290px;
  display: block;
}

.info {
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 70px;
  padding: 0 5px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0 5px;
  display: flex;
}

@media screen and (min-width: 650px) {
  .photo-card {
    flex-basis: calc(50% - 20px);
    width: 100%;
  }
}

@media screen and (min-width: 1310px) {
  .photo-card {
    flex-basis: calc(25% - 20px);
  }
}

.footer {
  z-index: 100;
  opacity: 0;
  background: linear-gradient(#82bcce 0%, #1c6ea4 100%, #82bcce 100%);
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  bottom: 0;
  transform: translateY(75px);
  box-shadow: -3px 0 5px 2px #000000bf;
}

.open {
  opacity: 1;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(0);
}

.message {
  color: #fff;
  text-align: right;
  max-width: 600px;
  margin-left: auto;
  margin-right: 15px;
  padding: 0;
  font-size: 20px;
  font-weight: 500;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2373a9;
  border: 2px solid #216b9dcb;
  border-radius: 5px;
  outline: none;
  width: 80px;
  height: 50px;
  margin-right: auto;
  padding-top: 0;
  font-size: 16px;
  font-weight: 400;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1);
}

.load-more:hover {
  transform: scale(1.05);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

